.container-weather {
    position: relative;
    background-color: rgba(93, 93, 93, 0.5);
    width: 1000px;
    margin: auto;
    margin-top: 20px;
    min-height: 700px;
}

.container-weather-top {
    display: flex;
    width: 100%;
    margin-left: 20px;
    flex-direction: column;
    margin-bottom: 20px;
}

.horizontal-divider {
    width: 100%;
    border-bottom: 1px solid black;
    margin-top: 20px;
    margin-bottom: 20px;
}

.weather-icon-cell {
    border-right: 1px solid black;
    padding: 10px;
}

.weather-info-cell {
    padding: 10px;
}

.weather-icon {
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.weather-temp {
    font-size: 70px;
    text-align: center;
}

.weather-wind-info {
    justify-content: space-between;
    display: flex;
    flex-direction: row;
}

.wind-dir-icon {
    max-height: 120px;
    max-width: 120px;
    margin-left: 20px;
    margin-top: 20px;
}

.tempdiagram {
    padding: 20px;
}

.mapGif {
    height: 540px;
    width: 290px;
    display: table-cell;
    vertical-align: middle;
    width: auto;
}

h4 {
    margin-top: unset;
}