.container-news {
    position: relative;
    background-color: rgba(93, 93, 93, 0.5);
    width: 1000px;
    margin: auto;
    margin-top: 20px;
    min-height: 470px;
}

.news-title-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.news-title {
    width: 950px;
    position: relative;
    margin-top: 10px;
    margin-bottom: 10px;
    text-align: center;
    text-decoration-line: underline;
    word-wrap: break-word;
}

@-webkit-keyframes scroll-left {
    0% {
        -webkit-transform: translateX(100%);
    }
    100% {
        -webkit-transform: translateX(-100%);
    }
}

@keyframes scroll-left {
    0% {
        -moz-transform: translateX(100%);
        /* Browser bug fix */
        -webkit-transform: translateX(100%);
        /* Browser bug fix */
        transform: translateX(100%);
    }
    100% {
        -moz-transform: translateX(-100%);
        /* Browser bug fix */
        -webkit-transform: translateX(-100%);
        /* Browser bug fix */
        transform: translateX(-100%);
    }
}

.news-body {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.news-image {
    max-height: 300px;
    height: auto;
    max-width: 450px;
    width: auto;
}

.content-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.news-body-text {
    max-height: 350px;
    max-width: 450px;
    text-align: center;
    line-height: 1.5rem;
}

.news-footer {
    display: flex;
    width: -webkit-fill-available;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px 20px;
    position: absolute;
    bottom: 4px;
}

.news-author {
    position: absolute;
    bottom: 4px;
}

.article-hidden {
    visibility: hidden;
    opacity: 0;
}

.article-fade-out {
    visibility: hidden;
    opacity: 0;
    transition: opacity 1s linear;
}

.article-fade-in {
    visibility: visible;
    opacity: 1;
    transition: opacity 1s linear;
}

.progress-bar {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 4px;
    background-color: rgba(93, 93, 93, 0.5);
    display: inline-block;
}

.progress-value {
    background-color: rgba(128, 128, 128, 1);
    transition: 0.1s all linear;
    height: 4px;
}

.progress-value.forward {
    animation: progress 30s linear forwards;
    -webkit-animation: progress-fwd 30s linear forwards;
}

.progress-value.backwards {
    animation: progress 1s linear backwards;
    -webkit-animation: progress-back 2s linear forwards;
}

@keyframes progress-fwd {
    from {
        width: 0%;
    }
    to {
        width: 100%;
    }
}

@-webkit-keyframes progress-fwd {
    from {
        width: 0%;
    }
    to {
        width: 100%;
    }
}

@keyframes progress-back {
    from {
        width: 100%;
    }
    to {
        width: 0%;
    }
}

@-webkit-keyframes progress-back {
    from {
        width: 100%;
    }
    to {
        width: 0%;
    }
}